@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;700;800;900&display=swap);
.Navigation_anchor{border-radius:10px;box-shadow:0 3px 5px rgba(0,0,0,0.08),0 3px 20px rgba(0,0,0,0.03);border:1px solid rgba(26,26,26,0.1)}.Navigation{display:flex;flex-direction:column;align-items:center;flex:0 0 12%}.Navigation_list{display:flex;flex-direction:column;list-style:none;grid-gap:15px;gap:15px}.Navigation_anchor{display:flex;flex-direction:column;align-items:center;justify-content:space-between;padding:10px 7px;width:120px;cursor:pointer;transition:all 0.1s, -webkit-transform 0.2s ease;transition:all 0.1s, transform 0.2s ease;transition:all 0.1s, transform 0.2s ease, -webkit-transform 0.2s ease}.Navigation_anchor img{width:70%;object-fit:cover;margin-bottom:8px}.Navigation_anchor p{font-size:1.2rem;text-align:center;padding:0 3px;text-transform:uppercase;font-weight:bold;color:#945036}.Navigation_anchor:hover{-webkit-transform:translateX(5px);transform:translateX(5px);box-shadow:0 3px 7px rgba(0,0,0,0.12),0 3px 25px rgba(0,0,0,0.07)}@media (max-width: 1000px){.Navigation{display:flex;flex-direction:row;justify-content:center;width:100%}.Navigation_list{display:flex;flex-direction:row;align-items:stretch;overflow-x:auto;padding:15px 20px;margin:-20px}.Navigation_anchor{height:100%}}@media (max-width: 700px){.Navigation_anchor{width:110px}.Navigation_anchor p{font-size:1.1rem}}

.avatar{border-radius:10px;box-shadow:0 3px 5px rgba(0,0,0,0.08),0 3px 20px rgba(0,0,0,0.03);border:1px solid rgba(26,26,26,0.1)}.SideAvatar{display:flex;flex:0 0 300px;flex-direction:column}.avatar{display:flex;flex-direction:column;align-items:center;padding:15px 15px 20px;transition:all 0.1s, -webkit-transform 0.2s ease;transition:all 0.1s, transform 0.2s ease;transition:all 0.1s, transform 0.2s ease, -webkit-transform 0.2s ease}.avatar_img{width:100%;height:50%;border-radius:5px;object-fit:cover;margin-bottom:20px}.avatar_desc{font-size:2rem;text-align:center}.avatar_anchor:first-child{margin-top:20px}.avatar_anchor{font-size:1.7rem;text-decoration:underline;display:block;margin:10px 0}.avatar_anchor:hover{color:#81c527}@media (max-width: 1200px){.SideAvatar{flex:0 0 200px}}@media (max-width: 1000px){.SideAvatar{width:300px}}

.ModalWindow{border-radius:10px;box-shadow:0 3px 5px rgba(0,0,0,0.08),0 3px 20px rgba(0,0,0,0.03);border:1px solid rgba(26,26,26,0.1)}.Modal{overflow:hidden;position:absolute;top:0;left:0;width:100vw;height:100vh;display:flex;justify-content:center;align-items:center;background-color:rgba(0,0,0,0.2)}.ModalActive{display:block}.ModalWindow{position:relative;display:flex;flex-direction:column;align-items:center;margin:0 auto;padding:40px;width:800px;min-height:300px;background-color:#fff}.ModalClose{font-size:3rem;position:absolute;top:10px;right:20px;cursor:pointer;color:#e63e3e}.ModalClose:hover{text-shadow:0 0 17px rgba(230,62,62,0.7)}

.TaskBlock{border-radius:10px;box-shadow:0 3px 5px rgba(0,0,0,0.08),0 3px 20px rgba(0,0,0,0.03);border:1px solid rgba(26,26,26,0.1)}.TasksList{display:flex;flex-wrap:wrap;justify-content:center;grid-gap:20px 20px;gap:20px 20px}.TaskBlock{text-align:center;flex:0 0 22%;padding:10px;border:1px solid rgba(26,26,26,0.1);transition:all 0.1s, -webkit-transform 0.2s ease;transition:all 0.1s, transform 0.2s ease;transition:all 0.1s, transform 0.2s ease, -webkit-transform 0.2s ease;cursor:pointer}.TaskBlock img{width:100%;margin-bottom:7px}.TaskBlock p{font-size:1.8rem;font-weight:bold;color:#945036;word-break:break-all}.TaskBlock:hover{-webkit-transform:scale(1.02);transform:scale(1.02);box-shadow:0 3px 7px rgba(0,0,0,0.08),0 3px 25px rgba(0,0,0,0.03)}@media (max-width: 700px){.TaskBlock{flex:0 0 30%}.TaskBlock p{font-size:1.6rem}}@media (max-width: 500px){.TaskBlock{flex:0 0 45%}}

.content{border-radius:10px;box-shadow:0 3px 5px rgba(0,0,0,0.08),0 3px 20px rgba(0,0,0,0.03);border:1px solid rgba(26,26,26,0.1)}*{box-sizing:border-box;padding:0;margin:0}html{font-size:10px}body{margin:0;font-family:'Rubik', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1.8rem;color:#131313}p>a{color:inherit;font-size:inherit;text-decoration:inherit;font-family:inherit}.wrapper{display:flex;min-height:100vh;max-width:1500px;grid-gap:25px;gap:25px;justify-content:space-between;margin:0 auto;padding:30px 18px}.content{flex:1 1 100%;padding:25px 20px;border-radius:10px}@media (max-width: 1000px){.wrapper{flex-direction:column;align-items:center}}

